@import './mixins.scss';

.text__start { 
    font-family: Rubik;
    font-weight: 400;
    font-size: 14px;

    &--M {
        font-size: 16px;
    }

    &--L {
        font-size: 20px;
    }

}

.text{
    font-family: Mulish;
    font-weight: 400;
    font-size: 14px;

    &--xs{
        font-size: 18px;
        font-weight: 700;
    }

    &--s{
        font-size: 20px;
    }

    &--m{
        font-size: 22px;
        font-weight: 700;
    }

    &--l{
        font-size: 24px;
        font-weight: 500;
    }

    &--xl{
        font-size: 36px;
        font-weight: 700;
    }
}

.after{

    &--new::after{
        content: 'New';
		background-color: $blue-light;
        @include after;
			
    }

    &--sale::after{
        content: 'Sale';
		background-color: $green;
		@include after;
    }
  



    &--30::after{
        content: '30%';
		background-color: $red;		
        @include after;
    }
  
}

    
  