.footer{
	width: 100%;
	height: 250px;
	display: flex;
	color: $gray-dark;

	&__info{
		flex: 1;
		padding-left: 40px;

		p{
			line-height: 200%;
		}

		p:first-child{
			font-weight: 500;
		}

		&__logo{
			background-image: url(../img/header/logo.png);
			background-repeat: no-repeat;
			padding: 15px 40px ;
			font-weight: 500;

		}

		&__contact{
			margin: 0 15px;
		}
	}


}

.last__line{
	width: 100%;
	height: 50px;
	display: flex;
	justify-content: space-between;
	border-top: 1px solid $gray;

	div{
		display: flex;
		margin: 0 40px;
		
		p{
			margin: 15px 20px;
		}
	}
}