.about__us{
	width: 400px;
	height: fit-content;
	margin: 30px 0;
	display: flex;

	&__info{
		flex: 1;
        text-align: center;

		img{
			float: none;
			margin: 0 10px;
            width: 30px;
            height: 30px;
		}

		h3{
			color: $gray-dark;
			font-size: 11px;
			margin: 7px 0 0 0;
		}

		p{
			color: $gray;
			margin-top: 3px;
            font-size: 12px;
		}
	}
}