.basket{

    margin: 30px;
    
    &__logo{
        font-size: 25px;
        font-weight: 600;
        color: $gray-dark
    }

    &__price{
        h3{
            font-size: 25px;
            margin-bottom: 0;
        }

        p{
            color: $gray;
            
        }

        .go__back{
            float: right;
        }
    }

    &__list{
        border: 1px solid $gray-light;
        border-radius: 10px;
        width: 100%;
        min-height: 600px;
        height: fit-content;
        background-image: '';
        background-repeat: no-repeat;
        background-size: 500px 500px;
        background-position: 45%;
        

        &__cart{
            display: flex;
            justify-content: space-around;
            background-color: $white;
            position: relative;
            border-radius: 10px 10px 0 0;
            border-bottom: 1px solid $gray-light;

            .cart__text{
                width: 30%;
                color: $gray-dark;
                text-align: justify;

                &--info{
                    color: $gray-light;
                    font-size: 10px;
                }
            }

            .cart__amount{
                color: $gray;
                display: flex;
                height: 50px;
                font-size: 20px;
                margin-top: 100px;
                
                div{
                    border: 1px solid $gray;

                    img{
                        width: 40px;
                        height: 40px;
                        padding-top: 5px;
                    }
                }

                &--number{
                    font-weight: 600;
                    padding: 13px 20px;
                }
            }

            .cart__price{
                color: $gray;
                margin: 15px 0 0 60px ;
                font-size: 20px;
                font-weight: 500;
            }

            .button__delete{
                
                width: 18px;
                height: 18px;
                margin: 10px;
                cursor: pointer;   
            }
        }
    }

    &__pay{
        
        float: right;
        margin: 20px;

        button{
            width: 100px;
            height: 40px;
        }
        
    }

    &__payment__form{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0,0,0,.5);
        display: none;
        z-index: 100;
    }
}