.offer{
	height: 580px;
	width: $main-width;
	display: grid;
	margin-left: 135px;
	grid-template-columns: 2fr 1fr;

	&__header{
		display: none;
	}

	h3{
		color: $gray-dark;
	}

	p{
		color: $green;
	}

	a{
		color: #757575;
		text-decoration: none;
	}
	
	&__furniture__big{
		grid-row-start: 1;
		grid-row-end: 3;
		margin: 10px;
		background-image: url(../img/offer/back_offer_main.png);
		background-size: 100% 100%;
		background-repeat: no-repeat;
		padding: 30px 0 0 30px;
		line-height: 40px;
	}

	&__furniture__small{
		margin: 10px;
		padding: 10px 0 0 20px;
		line-height: 40px;
	}

	#offer_small_first{
		background-image: url(../img/offer/back_offer_second1.png);
		background-size: 100% 100%;
		background-repeat: no-repeat;
	}

	#offer_small_second{
		background-image: url(../img/offer/back_offer_second2.png);
		background-size: 100% 100%;
		background-repeat: no-repeat;
	}
}