@import '../card.scss';

.our__products{

	width: 1173 / 1440 * 100%;
	height: 1100px;
	margin: 10px 9.3%;
	position: relative;

	h2{
		text-align: center;
		font-size: 36px;
	}

	&__list{
		height: auto;
		width: 100%;
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		padding: 20px;
		font-size: 16px;

		& .product__card{
			display: none;
		}
	}

	&__number__page{
		float: right;
		height: 44px;
		width: 570 /1440 * 100%;
		margin-top: 20px;
		display: flex;
		font-size: 17px;
		justify-content: end;

		& .paginator {
			line-height: 150%;
		  }
		& .paginator > span {
			width: 30px;
			height: 30px;
			display: inline-block;
			margin-right: 10px;
			cursor: pointer;
			text-align: center;

		  }
		& .paginator_active {
			background-color: $green;
			color: $white;
			
		  }
	}
}