.testimonials{

	width: 400px;
	height: fit-content;
	margin: 0;

	h2{
		text-align: center;
		font-size: 36px;
	}

	&__review{
		width: 390px;
		height: fit-content;
		margin: 30px 0;
        padding: 90px 10px 0 0 ;

		#avatar{
			top: 0px;
			left: 40%;
		}

		p:nth-child(2){
			padding: 0;
		}
	}
}

.blogs{
	width: 400px;
	height: 1500px;
	margin: 0;
	
	h2{
		text-align: center;
		font-size: 36px;
	}

	
	&__articles{
		display: inline-block;
		position: relative;
        height: fit-content;
		width: 100%;
		max-height: 2300px;

		&__news{

			width: 90%;
            height: 300px;
            margin: 0 0 10px 17px;
            display: block;

			&:nth-child(5){
				background-image: url(../img/blogs/blog-card4.png);
				display: none;
			}	
		}
	}
}

.increase_block{
				
	width: 90%;
	top: 0;
	left: 0;
	height: 100%;
	transform: none;
	font-size: 15px;

	& .blogs-news{

		&__text{
			display: block;
			font-size: 15px;
		}
	}

}