.header{
    width: 400px;
    background-color: $green;
    height: 20px;
    &__block{
        display: none;

        &:nth-child(1){
          margin-top: 3px;
            display: block;
            width: 400px;

            & p{
                margin: 0 120px;
            }
        }
    }
}

.top__bar{
    width: 100%;
    height: 220px;
    display: block;
  
    &__sections{
      overflow-y: hidden;
  
        img{
          width: 35px;
        }
  
        &__search{
          width: 400px;
          margin: 0;
  
        }

        &__button{ 
          margin: 0 0 0 -6px;
        }
  
        &__func{
          width: 100%;
          margin: 20px 0 0 20%;
          height: fit-content;
  
          & #basket::after{
            right: 65px;
          }
  
          &__icon{
  
            img{
              width: 24px;
              height: 24px;
            }
          }
        }
    }
  }