.herosections{
	width: 100%;
	height: 65vh;
	

	&__sitebar{
		width: 100%;
		height: 67px;
		display: flex;
		justify-content: flex-start;
		background-color: $back-color;

		&__all__collections{
			width: 260 / 1440 * 100%;;
			height: 67px;
			background-color: $green;
			color: $white;
			font-weight: 500;
			line-height: 18.96px;
			margin: 0 0 0 135 / 1440 * 100%;;
			background-image: url(../img/hero_sections/menu_bar.png);
			background-repeat: no-repeat;
			background-position: 14%;	

			p{
				text-align: center;
				margin: 26px 0 0 20px;
			}
		}

		&__navigation{
			display: flex;
			height: 20px;
			width: fit-content;
			margin: 25px 50px;

			&__category{
				margin: 0 25px;
			}
		}
	}

	&__collections{
		width: 260 / 1440 * 100%;;
		height: 454px;
		margin: 0 0 0 135 / 1440 * 100%;;
		float: left;
		display: grid;
		grid-template-rows: auto;
		background-color: $back-color;

		&__all{
			display: none;
		}

		p{
			border-bottom: 1px solid #E3E3E3;
			color: $gray-dark;
			margin: 10px 0 0 0;
			padding-left: 30px;
			

			&:last-child{
				border: none;
			}
		}
	}

	&__baner{
		width: 900 / 1440 * 100%;
		height: 430px;
		margin: 11px;
		float: left;
		background: linear-gradient(103.4deg, rgba(241, 250, 255, 0.88) 0.89%, #F1FAFF 101.61%);
		display: grid;
		grid-template-columns: 3fr 2fr 1fr;
		grid-template-rows: 0;

		&__text{
			padding: 72px 0 0 47px;

			p:first-child{
				color: $green;
				font-size: 22px;
				margin: 0;
			}

			h3{
				font-size: 40px;
				font-weight: 500;
				margin: 0;
			}

			p:nth-child(3){
				color: $gray;
				font-size: 25px;
				margin-top: 20px;
			}

			&__button__shop{
				width: fit-content;
				background: $green;
				border-radius: 5px;
				color:$white;
				letter-spacing: 2px;
				margin: 30px 0;
				padding: 12px;
			}
		}

		&__main__item{
			padding-top: 8%;
		}

		&__seconds{
			

			&__item{
				background: linear-gradient(180deg, rgba(255, 255, 255, 0.6) 0%, rgba(255, 255, 255, 0.56) 100%);
				border: 2px solid $white;
				border-radius: 8px;
				width: 90%;
				height: 110px;
				margin-top: 20px;
				text-align: center;

				h3{
					font-size: 12px;
					line-height: 26px;
					color: $green;
					margin: 0;
				}

				p{
					font-size: 12px;
					font-weight: 600;
					color: $gray-dark;
					margin: 0;
				}
			}
		}
	}
}