.basket{

    margin: 0;
    width: 380px;

    &__list{
        background-size: 300px 300px;
        

        &__cart{
            display: block;
            margin: 0 0 30px 20px;


            .cart__text{
                width: 100%;
            }

            .cart__amount{
                margin-top: 0px;
            }

            .button__delete{
                position: absolute;
                top: 0;
                right: 0;  
            }
        }
    }

    &__pay{
        
        float: right;
        margin: 20px;

        button{
            width: 100px;
            height: 40px;
        }
        
    }

    &__payment__form{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0,0,0,.5);
        display: none;
        z-index: 100;
    }
}