.header{
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 50px;
  position: relative;
  background-color: $green;
  color: $white;

  &__block{
      flex: 1;
      display:flex;
      flex: 1;
      
      p{
        margin: 15px 25%;

      }

      &__language{
        margin: 12px 0;
        padding: 0 20px;
        border-right: 1px solid black;
        cursor: pointer;
      }

      &__login{
        margin: 12px;
        cursor: pointer;
      }
  }
}

.top__bar{
  width: 100%;
  height: 92px;
  display: flex;
  flex: 1;

  &__sections{
      display: flex;
      flex: 1;
      position: relative;

      img{
        width: 34px;
        height: 39px;
        float: left;
        margin: 20px 10px 0 30%;
      }

      p{
        font-size: 24px;
        font-weight: 500;
        line-height: 28px;
        margin: 30px 0;
      }

      &__search{
        width: 90%;
        height: 45px;
        margin: 20px 0;
        padding: 0 20px;
        border: 2px solid $green;
        border-radius: 8px 0 0 8px;
        border-right: none;
        float: left;
        font-size: 13px;
        z-index: 10;

        &:focus {
          border: 2px solid $green;
          outline-color: $green; 
        }

        &--result{
          width: 85%;
          height: fit-content;
          overflow-y: scroll;
          overflow-x: hidden;
          max-height: 500px;
          background-color: $white;
          position: absolute;
          border-radius: 5px;
          top: 45px;
          left: 3px;
          z-index: 0;
          padding: 20px 2px 0 0;
         

          &::-webkit-scrollbar{
            background-color:rgba(0, 128, 0, 0); 
            width: 4px;
          }

          &::-webkit-scrollbar-thumb{
            background-color: $gray-light; 
            width: 4px;
          }

          & .item__result{
            width: 100%;
            height: auto;
            display: flex;
            justify-content: space-around;
            border: 1px solid $gray-light;
            cursor: pointer;

            p{
              font-size: 10px;
              margin: auto 20px;
            
            }

            img{
              margin: 5px;
              width: 50px;
              height: 50px;
            }
          }
        }
      }

      &__button{
        width: 55px;
        height: 41px;
        margin: 20px 0 0 -6px;
        padding: 0;
        border-left: none;
        float: right;
        z-index: 12;
        border: 2px solid $green;
        border-radius: 0 8px 8px 0;
        background-color: $green;
        background-image: url(../img/header/search.png);
        background-repeat: no-repeat;
        background-position: center;
        cursor: pointer;
      }

      

      &__func{
        width: 40%;
        height: 30px;
        display: flex;
        margin: 30px 30%;

        & #basket::after{
          content: attr(data-length);
          background-color: $green;
          color: $white;
          border-radius: 50%;
          width: 12px;
          height: 12px;
          font-size: 10px;
          text-align: center;
          position: absolute;
          top: 5px;
          right: 22px;
        }

        & #account{
          cursor: pointer;
        }

        &__icon{
          flex: 1;
          position: relative;

          img{
            width: 24px;
            height: 24px;
            margin: 5px 10px;
          }
        }
      }
  }
}