.about__us{
	width: $main-width;
	height: 58px;
	margin: 0 0 55px 12%;
	display: flex;

	&__info{
		flex: 1;

		img{
			float: left;
			margin: 0 20px;
		}

		h3{
			color: $gray-dark;
			font-size: 20px;
			margin: 7px 0 0 0;
		}

		p{
			color: $gray;
			margin-top: 3px;
		}
	}
}