.herosections{

height: fit-content;

    &__sitebar{
		display: none;
    }

	&__collections{
		width: 400px;
		height: fit-content;
		margin: 0 0 20px 0;
		float: none;
		background-color: none;

		&:hover > p {
			display: block;
		}

		&__all{
			display: block;
			width: 350px;
			margin: 0;
			padding: 22px 0 20px 125px;
			color: $white;
			background-color: $green;
			font-weight: 300;
			line-height: 18.96px;
			background-image: url(../img/hero_sections/menu_bar.png);
			background-repeat: no-repeat;
			background-position: 12%; 
			
			
		}
	

		p{
			display: none;
			cursor: pointer;
			font-size: 15px;
			margin: 10px 0;
		}
	}

	&__baner{
		display: none;
	}

}	
