.login__form{
    width: 99%;
    height: auto;
    justify-content: center;
    border: 1px solid $gray-light;
    border-radius: 5px;
    position: fixed;
    top: 160px;
    left: 0;

    & .close{
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        cursor: pointer;
    }
}