.special__offers{

	height: 354px;
	width: 1050 / 1440 * 100%;;
	margin: 30px 14%;
	position: relative;

	h2{
		text-align: center;
		font-size: 36px;
		margin-bottom: 30px;
	}

	&__offer{
		position: relative;
		width: 45%;
		height: 249px;
		float: left;
		background-image: url(../img/special_offer/special_offer_first.png); 
		background-repeat: no-repeat;
		background-size: 100% 100%;
		padding: 20px 0 0 20px;

		h3{
			font-size: 22px;
			line-height: 27px;
			color: $gray-dark;
		}

		p{
			margin: 15px 0 30px 0;
			width: 50%;
			font-size: 12px;
			line-height: 16px;
			color: #757575;
		}

		&__price{
			height: 30px;
			width: fit-content;
			display: flex;
			flex: 1;
			padding-left: 10px;

			h3{
				color: $green;
				line-height: 100%;
				margin-top: 7px;
			}

			p{
				text-decoration-line: line-through;
				color: $gray-light;
				font-size: 18px;
				line-height: 120%;
				margin: 7px 0 0 20px;
			}

			&__button{
				color: $white;
				background-color: $green;
				font-size: 12px;
				text-align: center;
				width: 100px;
				padding-top: 7px;
				border-radius: 5px;
			}
		}

		&__timeout{
			display: flex;
			margin: 20px 0 0 8px;

			&__delay{
				background-color: $green;
				color: $white;
				border-radius: 50%;
				margin-right: 6px;
				text-align: center;
				font-size: 15px;
				line-height: 120%;
				color: $white;
				padding: 10px 14px;

				p{
					font-size: 9px;
					line-height: 120%;
					margin: 0;	
					color: $white;
					font-weight: 300;
				}


			}
		}

		&::after{
			content: '40% off';
			background-color: $blue-light;
			border-bottom-left-radius: 5px;
			font-size: 12px;
			color: $white;
			padding: 8px;
			position: absolute;
			top: 0;
			right: 0;
		}
	}

	&__offer:last-child{
		float: right; 
		background-image: url(../img/special_offer/special_offer_second.png); 
		
		&::after{
			content: '50% off';
		}

	}
}