.special__offers{

	height: fit-content;
	width: 400px;
	margin: 0 0;

	&__offer{
		width: 90%;
		float: none;
        margin: 10px auto;

        &__timeout{
    
            &__delay{
    
                p{
                    font-size: 7px;
                    width: 15px;
                }
    
    
            }
        }
    }

    &__offer:last-child{
		float: none; 
    }

    
}
