.product__card{
    width: 270px;
    height: fit-content;
    margin: 0;
    font-size: 10px;

    &:hover > .item__name,
    &:hover > .item__price,
    &:hover > .item__rice--old,
    &:hover > .rating{
        opacity: 1;
        transition: 1s;
    }

    & .item_buy, .item_wish{
        width: 19px;
        height: 19px;
    }  
    
    .checkmark {
        width: 30px;
        height: 30px;
    }

    & .item__name, .item__price, .item__price--old, .rating {
        opacity: 0;
        font-size: 15px;
        margin: 15px 0;
    }

    & .rating{
        font-size: 11px;
    }
}

