.product__card{
    width: 270px;
    height: fit-content;
    position: relative;
    color: $gray-light;
    line-height: 10px;
    margin-bottom: 40px;

    &:hover{
        box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
        transition: 0.5s;
        transform: scale(1.01);
    }

    & .item__image{
        width: 270px;
        height: 288px;
        position: relative;

        img{
          width: 270px;
          height: 288px;
        }

        & .item_button{
            position: absolute;
            bottom: 10px;
            right: 10px;

            & .item_buy, .item_wish{
                background-color: $white;
                box-shadow: 0px 2px 33px rgba(0, 0, 0, 0.09);
                border-radius: 50%;
                width: 23px;
                height: 23px;
                padding: 10px;
                margin: 5px 0;
                cursor: pointer;  
                stroke: $gray;
            }  

            & .item_buy{
                background-image: url(../img/card/add.png);
                background-repeat: no-repeat;
                background-position: 50%;
                background-size: 50%;
                
                & svg{
                    display: none;
                }
            }

            & .active{
                background-color: none;
                transition: 1s;
                pointer-events: none;
                opacity: 1;

                & svg{
                    display: block;
                }

                .checkmark {
                    position: absolute;
                    bottom: 4px;
                    left: 0;
                    width: 44px;
                    height: 44px;
                    border-radius: 50%;
                    display: block;
                    stroke-width: 2;
                    stroke: $white;
                    stroke-miterlimit: 10;
                    box-shadow: inset 0px 0px 0px $green;
                    animation: fill .4s ease-in-out .4s forwards, scale .3s ease-in-out .9s both;
                  }
                  
                  .checkmark__circle {
                    stroke-dasharray: 166;
                    stroke-dashoffset: 166;
                    stroke-width: 2;
                    stroke-miterlimit: 10;
                    stroke: $green;
                    fill: none;
                    animation: stroke .6s cubic-bezier(0.650, 0.000, 0.450, 1.000) forwards;
                  }
                  
                  .checkmark__check {
                    transform-origin: 50% 50%;
                    stroke-dasharray: 48;
                    stroke-dashoffset: 48;
                    animation: stroke .3s cubic-bezier(0.650, 0.000, 0.450, 1.000) .8s forwards;
                  }
                  
                  @keyframes stroke {
                    100% {
                      stroke-dashoffset: 0;
                    }
                  }
                  
                  @keyframes scale {
                    0%, 100% {
                      transform: none;
                    }
                    50% {
                      transform: scale3d(1.1, 1.1, 1);
                    }
                  }
                  
                  @keyframes fill {
                    100% {
                      box-shadow: inset 0px 0px 0px 30px $green;
                    }
                  }
            }
        }
    }

    

    & .item__name{
        color: $gray-dark;
        height: fit-content;
    }

    & .item__price{
        color: $green;
        float: left;
        margin-right: 10px;
        height: fit-content;
    }

    & .item__price--old{
        float: left;
        text-decoration: line-through;
    }

    & .rating{
        display: flex;
        width: 70px;
        height: 11px;
        float: right;
        margin: 20px;
    }

}