.login__area{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.5);
    display: none;
    z-index: 100;
}

.login__form{
    width: 30%;
    height: auto;
    justify-content: center;
    border: 1px solid $gray-light;
    border-radius: 5px;
    position: fixed;
    top: 160px;
    left: 35%;
    background-color: $white;
    opacity: 1 !important;
    z-index: 100;

    & .close{
        position: absolute;
        top: -40px;
        right: 0px;
        cursor: pointer;
    }

    & .login__choise{
        display: flex;
        width: 100%;
        height: 50px;
        text-align: center;
        margin-bottom: 20px;
        cursor: pointer;

        & .login{
            padding-top: 15px;
            width: 50%;
        }

        & .registration{
            padding-top: 15px;
            background-color: $gray-light;
            width: 50%;
        }
    }

    .info--login, .info--registration{
        text-align: center;

        .email__login, .password__login, .email__registration, .password__registration, .nickname__registration{
            position: relative;
           

            &::after{
                content: attr(data-error);
                color: red;
                position: absolute;
                top: 65px;
                left: 20px;
                font-size: 12px;
            }

            & input{
                width: 80%;
                height: 40px;
                margin: 20px 10px;
                border: 1px solid $gray-light;
                border-radius: 3px;
                padding-left: 40px;
                font-size: 15px;
                background-repeat: no-repeat;
                background-size: 20px 20px;
                background-position: 10px;
            }

            & .email__input--login, .email__input--registration{
                background-image: url(../../img/login/email.png);
            }

            & .password__input--login, .password__input--registration{
                background-image: url(../../img/login/password.png);
            }

            & .nickname__input--registration{
                background-image: url(../../img/login/nickname.png);
            }
        }
    
    }

    .info--registration{
        display: none;
    }

    
    .remember{
        margin: 15px 30px;
    }

    .confirm--login, .sign--out, .confirm--registration{
        width: 80%;
        margin: 20px 40px;
        height: 40px;
        background-color: $green;
        color: $white;
        border: none;
        cursor: pointer;

        &:active{
            width: 79.5%;
            background-color: #65a742;
        }

        &::after{
            content: attr(data-error);
            color: red;
            position: absolute;
            bottom: 70px;
            left: 65px;
            font-size: 12px;
        }
    }

    .sign--out{
        display: none;
    }

    .registration--complete{
        display: none;
    }

}