@import '../../typography.scss';
@import '../card.scss';

.trending{

	width: $main-width;
	height: 1050px;
	margin-left: 135 / 1440 * 100%;;
	

	&__categories{
		height:30px;
		width: 572px;
		margin: 45px (299 + 90) / 1440 * 100%;
		display: flex;
		font-size: 16px;
		line-height: 30px;
		color: $gray-light;

		&__sort{
			margin: 0 45px;
			cursor: pointer;
		}
	}

	&__product__list{
		height: 780px;
		width: (1170 + 270) / 1440 * 100%;
		margin: 30px 0;
		
		
		&--top, &--new, &--best{
			display: none;
			grid-template-columns: repeat(4, 1fr);
			padding: 20px;
		}

		&--top{
			display: grid;
		}
	}
}

.sort--active {
	color: $green;
}

