body {
	margin: auto;
	max-width: 1440px;
	justify-content: center;
	top: 0 !important;
  
	& .skiptranslate {
		display: none;
		height: 0;
	}

		h2{
			color: $gray-dark;
			font-weight: 700;
			text-align: center;
			font-size: 36px;
		}

	&::-webkit-scrollbar{
		width: 13px;
		background-color: rgba(0, 0, 0, 0);
	}

	&::-webkit-scrollbar-thumb{
		background-color: $gray-light;
		border-radius: 10px;
	}
}

a{
	color: $gray;
	text-decoration: none;
}


	

