@import '../blogs-news/blogs-news.scss';

.testimonials{

	width: 1034 / 1440 * 100%;
	height: 447px;
	margin: 30px 14%;

	h2{
		text-align: center;
		font-size: 36px;
	}

	&__review{
		width: 85%;
		height: 312px;
		margin: 80px 7.3%;
		background: #F8FAFB;
		box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.05);
		border-radius: 8px; 
		position: relative;
		justify-content: center;
		text-align: center;
		color: $gray-dark;

		#avatar{
			position: absolute;
			width: 90px;
			height: 90px;
			top: -40px;
			left: 45%;
			background-color: white;
			border-radius: 50%;
		}

		p:nth-child(2){
			padding: 80px 125px 40px 125px;
		}
	}
}

.blogs{
	width: 1173 / 1440 * 100%;
	height: 1040px;
	margin: 30px 9.3%;
	
	h2{
		text-align: center;
		font-size: 36px;
	}

	
	&__articles{
		display: flex;
		flex-wrap: wrap;
		position: relative;
		gap: 20px;
		color: $white;
		overflow: hidden;
		max-height: 950px;
		border-radius: 10px;

		&__news{

			width: 49%;
			display: flex;
			justify-content: center;
			height: 455px;
			border-radius: 8px;
			background-repeat: no-repeat;
			background-size: 100% 100%;
			overflow: hidden;

			& .blogs-news__text{
				display: none;
			}

			&:first-child{
				background-image: url(../img/blogs/blog-card1.png);				
			}
	
			&:nth-child(2){
				background-image: url(../img/blogs/blog-card2.png);				
			}
	
			&:nth-child(3){
				background-image: url(../img/blogs/blog-card3.png);				
			}
	
			&:nth-child(4){
				background-image: url(../img/blogs/blog-card4.png);
			}

			&:nth-child(5){
				background-image: url(../img/blogs/blog-card4.png);
			}
			
		}
	
	}

}

.increase_block{
				
	position: absolute;
	left: 26%;
	top: 26%;
	transition: 1s;
	border-radius: none;
	transition: 1s;
	transform: scale(2.07);
	font-size: 7px;

	& .blogs-news{
		padding: 40px;

		&__text{
			display: block;
			font-size: 7px;
			white-space:pre-line;
		}

		&__read{
			display: none;
			width: 100%;
		}

		div{
			margin: 3px 10px;
		}

		h3{
			font-size: 19px;
		}
	}

}