.trending{

	width: 400px;
	height: fit-content;
	margin: 0;
	

	&__categories{
		display: none;
	}

	&__product__list{
		height: fit-content;
		width: 400px;
		margin: 30px 35px;
		
		
		&--top, &--new, &--best{
			display: none;
			padding: 20px;
		}

		&--top{
			display: block;
		}
	}
}
